<template>
    <v-container class="my-6" fluid style="max-width: 1000px;">
        <h2 class="text-h5 grey--text text--darken-2"><v-icon large>mdi-dog</v-icon> {{$t('strings.soothe_your_dog')}}</h2>
        <v-divider class="mb-5"></v-divider>
        <v-card max-width="300">
            <v-img
                :src="require('@/assets/images/dogs/1.jpg')"
                class="white--text align-end"
                gradient="to bottom, rgba(0, 0, 0, .1), rgba(0, 0, 0, .5)"
                height="200px">
                <div class="d-flex align-center pa-3">
                    <v-card-title class="pa-0">{{$t('strings.track_number', { number: 1 })}}</v-card-title>
                    <v-btn @click="togglePlay(1)" :class="{'ml-auto': !$vuetify.rtl, 'mr-auto': $vuetify.rtl}" color="white" icon>
                        <v-icon v-if="currentlyPlaying !== 1">mdi-play-circle-outline</v-icon>
                        <v-icon v-else>mdi-stop-circle-outline</v-icon>
                    </v-btn>
                </div>
            </v-img>
            <v-overlay opacity=".8" absolute v-if="loading === 1">
                <div class="d-flex flex-column align-center">
                    <v-progress-circular
                        size="25"
                        width="4"
                        indeterminate
                        color="primary"></v-progress-circular>
                    <span class="my-2">{{$t('strings.loading_track')}}</span>
                    <v-btn @click="cancelLoading" icon small>
                        <v-icon>mdi-close-circle-outline</v-icon>
                    </v-btn>
                </div>
            </v-overlay>
        </v-card>
    </v-container>
</template>

<script>
export default {
    data() {
        return {
            currentlyPlaying: null,
            loading: null,
            currentlyPlayingAudio: null,
        }
    },
    methods: {
        cancelLoading() {
            this.loading = null;
            this.currentlyPlaying = null;
            if(this.currentlyPlayingAudio) {
                this.currentlyPlayingAudio.pause();
                this.currentlyPlayingAudio = null;
            }
        },
        togglePlay(track) {
            if(track === this.currentlyPlaying) {
                this.cancelLoading();
                return;
            }
            var audio = new Audio(require(`@/assets/media/dogs/${track}.mp3`));
            this.loading = track;
            audio.onplay = () => {
                this.currentlyPlaying = track;
                this.currentlyPlayingAudio = audio;
                this.loading = null;
            };
            audio.play();
        }
    },
    name: 'dogs-index'
}
</script>